	module.exports = {"data":{"wordpressPost":{"title":"How To Create This Gorgeous Grey French Manicure + More Nail Ideas","content":"<p>Fancy a killer new nail look? This gorgeous <a href=\"https://au.sallyhansen.com/whats-new/article/trending-now-gray-french-manicure\">grey French manicure</a> is just the thing.</p>\n<p>It&#8217;s chic, stylish, subtle and yet, a touch bold. Better still, it&#8217;s quick and incredibly easy.</p>\n<p>Simply apply a base coat in a light pink or nude shade. Then apply a half moon in one shade of grey and then a second slightly further down the nail bed.</p>\n<p>According to <strong>Madeline Poole, Global Colour Ambassador for Sally Hansen</strong>, the trick to making this look work is by using two shades of cool grey nail polish in contrasting textures. Madeline recommends Miracle Gel in Buffalo Nickel and <a href=\"https://au.sallyhansen.com/nails/nail-color/gel-nail-color/miracle-gel\">Greyfitti.</a> But really, any greys will work. In fact, any two shades of the same colour will work if grey is not for you.</p>\n<p>This look also works beautifully when you swap the greys for maroons, reds, purples even pinks.</p>\n<img class=\"alignnone size-medium wp-image-3415\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/13233520/JPG-OPI-136x300.jpg\" alt=\"\" width=\"136\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/13233520/JPG-OPI-136x300.jpg 136w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/13233520/JPG-OPI.jpg 213w\" sizes=\"(max-width: 136px) 100vw, 136px\" />\n<p>We&#8217;re pretty keen on <a href=\"http://shop.davidjones.com.au/djs/ProductDisplay?catalogId=10051&amp;productId=15090&amp;langId=-1&amp;storeId=10051\">OPI Nail Lacquer in Feeling Hot Hot Hot, $19.95.</a></p>\n<p>In the past we would have always recommended a top coat too for extra shine and also, to prevent chips and scratches. Sally Hansen Miracle Gel comes with its own top coat that will keep your hot new nails ship shape for 14 days or even longer. Genius!</p>\n<p>&nbsp;</p>\n<img class=\"alignnone size-medium wp-image-3413\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/13232642/JPG-Jamberry-300x163.jpg\" alt=\"\" width=\"300\" height=\"163\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/13232642/JPG-Jamberry-300x163.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/13232642/JPG-Jamberry.jpg 533w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p>&nbsp;</p>\n<p>If you don&#8217;t think you can pull off this look you can always try nail wraps. These are stick on nails that you pop on, trim with scissors to your own nail shape and voila &#8211; brand new nail styles. We love these ones from <a href=\"https://www.jamberry.com/au/en/shop/shop/for/nail-wraps?collection=collection%3A%2F%2F1120&amp;pageSize=24\">Jamberry Nails.</a></p>\n<p>&nbsp;</p>\n<p>&nbsp;</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/13223625/JPG-Grey-French.jpg"},"categories":[{"slug":"lifestyle","name":"Lifestyle"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"1c0a5043-dbdf-5d8b-b8ab-9f92be65dde3"}}

